<template>

  <div>
    <h3>角色信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col :xs="24" :sm="24" :md="12" :lg="12">
        <Form :model="formData" :label-width="80">

          <FormItem label="名称">
            <Input v-model="formData.name" placeholder="请填写名称"></Input>
          </FormItem>

          <FormItem label="状态">
            <Select v-model="formData.status">
              <Option label="禁用" :value="0"></Option>
              <Option label="正常" :value="1"></Option>
            </Select>
          </FormItem>

          <FormItem label="排序">
            <Input v-model="formData.sort_num" placeholder="请填写排序"></Input>
          </FormItem>

          <FormItem label="角色类型">
            <Select v-model="formData.type">
              <Option label="内置角色" :value="0"></Option>
              <Option label="自建角色" :value="1"></Option>
            </Select>
          </FormItem>
          <FormItem label="运营团队">
            <Select v-model="formData.operation_team_id">
              <Option v-for="(item,index) in teams" v-bind:key="index" :label="item.name"
                      :value="item.id"></Option>
            </Select>
          </FormItem>
          <FormItem label="简介">
            <Input v-model="formData.description" type="textarea" :rows="4" placeholder="请填写简介"></Input>
          </FormItem>
          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>
        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import teamApi from '../../operating/api'
import api from './api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {},
      teams: [],
      load: false
    }
  },
  mounted () {
    this.loadTeam()
    if (this.$route.query.id != null) {
      this.loadData()
    }
    if (this.$route.query.operation_team_id != null) {
      this.formData.operation_team_id = parseInt(this.$route.query.operation_team_id)
    }
  },
  methods: {
    loadTeam () {
      teamApi.select((res) => {
        console.assert(res)
        this.teams = res.data.data
      })
    },
    loadData () {
      this.load = true
      api.detail({ id: this.$route.query.id }, res => {
        this.load = false
        this.formData = res.data.data
      }, () => {
        this.load = false
      })
    },
    submit () {
      if (!this.$formUtil.isStrValid(this.formData.name)) {
        this.$Message.error('请输入名称')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.status)) {
        this.$Message.error('请选择状态')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.sort_num)) {
        this.$Message.error('请输入排序')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.type)) {
        this.$Message.error('请选择角色类型')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.operation_team_id)) {
        this.$Message.error('请选择运营团队')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.description)) {
        this.$Message.error('请输入简介')
        return
      }
      this.load = true
      api.add(this.formData, res => {
        this.load = false
        this.$router.back()
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

